* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
}
.wrap {
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: auto;
}

body {
  background-color: #0e0e2e;
}

.hidden {
  display: none !important;
}

.background {
  padding: 57px 0;
  width: 100vw;
  background-color: #0e0e2e;
  min-height: 100vh;
  background-image: url('../assets/images/starryStar.png');
  background-repeat: repeat-y;
  background-size: contain;
  height: 10000000px;
  display: flex;
  flex-direction: column;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex {
  display: flex;
}

.alignCenter {
  display: flex;
  align-items: center;
}

.justifyCenter {
  display: flex;
  justify-content: center;
}

.directionColumn {
  display: flex;
  flex-direction: column;
}

.imgObjectFitContain {
  object-fit: contain;
}

.positionAbsolute {
  position: absolute;
}

.positionRelative {
  position: relative;
}

.firstWorldOnlyOne {
  margin: 17.7px 0 42.6px;
  font-family: MaruBuriot;
  font-size: 34.9px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  text-align: center;
  color: #e4ba9b;
}

.flag {
  position: absolute;
  bottom: -27px;
}

.atLastFont {
  margin: 6.8px 0 34.8px;
  font-family: MaruBuriot;
  font-size: 23.9px;
  font-weight: 200;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.09;
  letter-spacing: normal;
  text-align: center;
  color: #e4ba9b;
}

.fingerFrame {
  border: 40px solid;
  border-image-source: url('../assets/images/fingerFrame2.png');
  width: 788px;
  margin: 100px 0;
  border-image-slice: 40;
  display: flex;
  flex-direction: column;
}

.lastStoryOpen {
  font-family: MaruBuriot;
  font-size: 29.9px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: #e4ba9b;
  height: 50px;
  margin-top: -20px;
}

.fingerToggleContainer {
  bottom: -188px;
  width: 100%;
}

.fingerToggle {
  background: url('../assets/images/fingerDown.png') no-repeat;
  width: 87px;
  height: 179px;
}
.open .fingerToggle {
  background: url('../assets/images/fingerUp.png') no-repeat;
  width: 87px;
  height: 179px;
}
.storyListContainer {
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
.open .storyListContainer {
  max-height: 800px;
  overflow: auto;
}

.storyItemContainer {
  position: relative;
  display: flex;
  padding: 25px 0;
  margin: 0 10px;
  justify-content: space-between;
}
.storyItemContainer:not(:last-child) {
  border-bottom: 1px solid rgba(228, 186, 155, 0.5);
}
.storyItemContainer > .flex > img {
  margin-right: 25px;
}

.storyTitleFont {
  font-family: MaruBuriot;
  font-size: 23.9px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.09;
  letter-spacing: normal;
  color: #fff;
}
.storyContentFont {
  font-family: MaruBuriot;
  font-size: 18px;
  font-weight: 200;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  color: #fff;
}

.fadeContainer {
  margin-top: 150px;
}

.fadeContainer .hiarline {
  width: 1px;
  height: 76px;
  background-color: rgba(228, 186, 155, 0.5);
  margin: 17px;
}

.fadeContainer .fadeFont {
  font-family: MaruBuriot;
  font-size: 40px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: -1.03px;
  text-align: center;
  color: #e4ba9b;
}

.fadeContainer > div {
  position: fixed;
  width: 100%;
}
.fadeContainer > div:not(.layer0) {
  opacity: 0;
}
